.CreateTodoButton {
  display: flex;
  justify-content: center;
  padding-top: 12px;
}

.CreateTodoButton .add-btn{
  border: none;
  opacity: 0.6;
  outline: none;
  color: #fff;
  cursor: pointer;
  font-size: 13px;
  padding: 7px 13px;
  border-radius: 4px;
  letter-spacing: 0.3px;
  transition: transform 0.25s ease;
  background: black;
}
.add-btn:hover {
  background: linear-gradient(135deg, #1798fb 0%, #3c87ff 100%);
}
